/* Base Styles -------------------- */
* {
  box-sizing: border-box; }

body {
  font-size: 1em;
  line-height: 1.5;
  color: #878787;
  margin: 0;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif; }

h1,
h2 {
  font-family: "Abolition Regular", Helvetica, Arial, sans-serif; }

h2 {
  font-size: 3.3125em;
  line-height: 1.1;
  font-weight: normal;
  margin: 0 0 .5em; }

h3 {
  font-size: 1.25em;
  line-height: 1.2;
  color: #48525c;
  margin-bottom: 1.7em; }

img {
  max-width: 100%;
  margin-bottom: 20px;
  border-radius: 10px; }

ul,
ol {
  margin: 30px 0; }

li {
  margin-bottom: 10px; }

/* Pseudo-classes ------------------ */
a:link {
  color: #ffa949;
  text-decoration: none; }

a:visited {
  color: #add8e6; }

a:hover {
  color: rgba(255, 169, 73, 0.5); }

/* Web Fonts -------------------- */
@font-face {
  font-family: "Abolition Regular";
  src: url("../fonts/abolition-regular-webfont.eot");
  src: url("../fonts/abolition-regular-webfont.eot?#iefix") format("embedded-opentype"), url("../fonts/abolition-regular-webfont.woff") format("woff"), url("../fonts/abolition-regular-webfont.ttf") format("truetype"); }

.main-heading {
  font-size: 5.625rem;
  line-height: 1.3;
  font-weight: normal;
  color: #fff;
  text-transform: uppercase;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.8);
  margin: 12px 0 0; }
  @media (max-width: 768px) {
    .main-heading {
      font-size: 5rem;
      line-height: 1.1; } }

.main-title {
  font-size: 1.625rem;
  font-weight: 200;
  color: #fff;
  letter-spacing: .065em;
  border-bottom: 2px solid;
  padding-bottom: 10px; }
  @media (max-width: 768px) {
    .main-title {
      font-size: 1.3rem;
      border: none; } }

.intro {
  font-size: 1.25em;
  line-height: 1.6;
  color: initial; }
  @media (max-width: 768px) {
    .intro {
      font-size: 1rem; } }

.callout {
  font-size: 1.25em;
  border-bottom: 3px solid;
  padding: 0 9px 3px;
  margin-top: 20px;
  display: inline-block; }

.arrow {
  width: 50px;
  margin-top: 150px; }
